ly.incomes,\n                        color: 'info',\n                    }, {\n                        data: this.envelopeDevelopment.yearly.outcomes,\n                        color: 'danger',\n                    },\n                ]\n            },\n\n        },\n\n    }\n\n</script>\n\n\n\n<style scoped>\n\n    .tab-pane {\n        padding: 20px;\n    }\n\n    .btn-link {\n        cursor: pointer;\n        padding: 0px 5px;\n        visibility: hidden;\n    }\n\n    .active .btn-link {\n        visibility: visible;\n    }\n\n</style>\n"],"sourceRoot":"webpack://"}]);

